import React , {useState} from 'react'
import '../node_modules/bootstrap/dist/css/bootstrap.css';
import { Button,Alert } from 'react-bootstrap';
function Bootstrapupper(props) {
  
  const [Text,setText] = useState("Enter text here");
  const ConvertToUpperCase = () =>
  {
     let newText= Text.toUpperCase();
      setText(newText);
      props.showMessage("Converted to uppercase");
  }
  const TextChange = (event)=>
  {
        setText(event.target.value);
  }
  const ConvertToLowerCase=()=>{
      let newText = Text.toLowerCase();
      setText(newText);
      props.showMessage("Converted to lowercase");
  }
  const ClearText =()=>{
      setText("");
      props.showMessage("Text Cleared");
  }
  const [mystyle,setmystyle] =useState({
      color:"Black",
      backgroundColor:"white"
  });
  const [TextDark,setTextDark]=useState("Set Dark Mode");
  const ToggleDarkMode= ()=>{
        if(mystyle.color==="Black"){
            setmystyle({
            color:"White",
            backgroundColor:"Black"
            });
            setTextDark("Set Light Mode");
            props.showMessage("Dark Mode Enabled");
        }
        else{
        setmystyle({
            color:"Black",
            backgroundColor:"white"
            });
            setTextDark("Set Dark Mode");
            props.showMessage("Light Mode enabled");
        
        }

  }
    return (<>
            
       <div className='container px-5' style ={mystyle}>
           
        <h3 className='text-center my-5'> Text Converter</h3>
        <div className= "form">
            <textarea type="text" className='form-control' onChange={TextChange} rows="8" value={Text}></textarea>
            <Button variant='primary' className='my-3 py-1 mx-2' onClick={ConvertToUpperCase}>Convert to uppercase</Button>
            <Button variant='primary' className='my-3 py-1 mx-2' onClick={ConvertToLowerCase}>Convert to Lowercase</Button>
            <Button variant='primary' className='my-3 py-1 mx-2' onClick={ClearText}>Clear Text</Button>
            <Button variant='primary' className='my-3 py-1 mx-2' onClick={ToggleDarkMode} >{TextDark}</Button>
            
            <p className='my-2'>Number of words in text are {Text.split(" ").length} and charactes are {Text.length}</p>
        </div>

    </div>
    </>
  )
}

export default Bootstrapupper